import React from "react"
import './styles/home.scss'
import TopBanner from './components/TopBanner/TopBanner'
import PageComponent from "../components/PageComponent/PageComponent"
import FeaturedProductLeft from "./components/FeaturedProductLeft/FeaturedProductLeft"
import FeaturedProductRight from "./components/FeaturedProductRight/FeaturedProductRight"

const HomePage = () => {
    return (
        <>
            <PageComponent label={'Banner 1 hình'}>
                <TopBanner/>
            </PageComponent>
            <PageComponent label={'Sản phẩm nổi bật 01'}>
                <FeaturedProductLeft/>
            </PageComponent>
            <PageComponent label={'Sản phẩm nổi bật 02'}>
                <FeaturedProductRight/>
            </PageComponent>
        </>
    )
}

export default HomePage
