import React from "react"
import './styles/product-card.scss'
import emptyProductImage from '@src/assets/images/svg/empty-product-image.svg'

const ProductCard = (props) => {
    return (
        <div className={'product-card'}>
            <div className="product-card__image">
                <div className="product-card__position">{props.product.position}</div>
                <img src={emptyProductImage} alt=""/>
            </div>
            <div className="product-card__details">
                <div className="tags">
                    <div className="tag">nhãn hiệu</div>
                </div>
                <div className="product-card__name">Tên sản phẩm</div>
                <div className="product-card__price">giá sản phẩm</div>
            </div>
        </div>
    )
}

export default ProductCard
