import React, {useState} from "react"
import './styles/feature-product-right.scss'
import emptyImage from '@src/assets/images/svg/image-empty.svg'
import ProductCard from "../ProductCard/ProductCard"

const FeaturedProductRight = () => {
    const initProductList = [{position: 1}, {position: 2}, {position: 3}, {position: 4}]
    const [productList, setProductList] = useState(initProductList)
    return (
        <>
            <div className="card w-100">
                <div className={'feature-product-right'}>
                    <div className="featured-products">
                        {
                            productList && productList.map((item, index) => {
                                return (
                                    <ProductCard key={index} product={item}/>
                                )
                            })
                        }
                    </div>
                    <div className={'featured-image'}>
                        <img src={emptyImage} alt=""/>
                    </div>
                </div>
            </div>
        </>
    )
}

export default FeaturedProductRight
