import React, {useState} from "react"
import './styles/top-banner.scss'
import FormTopBanner from "./FormTopBanner/FormTopBanner"

const TopBanner = () => {
    const [isShowForm, setShowForm] = useState(false)
    return (
        <>
            <div className={'card w-100 rounded-0 position-relative'} onClick={() => {
                setShowForm(true)
            }}>
                <div className={'top-banner'}>
                    <img src={'https://dev.feaer.vn/static/media/im_banner.97bf2196.jpg'} alt=""/>
                    <div className={'ratio'}>Tỷ lệ ảnh: 1100/450</div>
                </div>
            </div>
            <FormTopBanner status={isShowForm} toggle={() => {
                setShowForm(false)
            }}/>
        </>
    )
}

export default TopBanner
