import React from "react"
import {Input} from "reactstrap"

const FormTopBanner = (props) => {

    return (
        <div className={`page-component-form ${props.status ? ' active' : ''}`}>
            <div className="card w-100">
                <div className="form-group">
                    <label>Banner</label>
                    <Input type={'file'}/>
                    <label className={'mt-1 mr-1'}><input type={'radio'} className={'mr-50'} name={'banner-type'} checked={true}/>
                        Banner 1 hình
                    </label>
                    <label className={'mt-1 mr-1'}><input type={'radio'} className={'mr-50'} name={'banner-type'}/>
                        Banner slider
                    </label>
                </div>
                <div className="text-right">
                    <button className="btn btn-primary" onClick={props.toggle}>LƯU</button>
                </div>
            </div>
        </div>
    )
}

export default FormTopBanner
